.Table {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	-ms-flex-pack: start;
	    justify-content: flex-start;
	-ms-flex-align: start;
	    align-items: flex-start;
	max-width: 480px;
	width: 100%;
	margin: 24px auto;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}

.Cell {
	width: 0%;
	padding: calc(12.5% - 1px);
	border: 1px solid #ccc;
	cursor: pointer;
	position: relative;
	font-size: 64px;
}

.CellContent {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;


}
.Cell:hover {
	background-color: #eee;
}

.CellContent:empty {
	background-color: #ccc;
}

@media screen and (max-width: 414px) {
	.Cell {
		font-size: 48px;
	}

	.Cell:hover {
		background-color: transparent !important;
	}
}

@media screen and (max-width: 374px) {
	.Cell {
		font-size: 32px;
	}
}

@media screen and (max-width: 319px) {
	.Cell {
		font-size: 28px;
	}
}



